import {
	router
} from '@/common/router.js'

import Vue from 'vue'

export default {
	/**
	 * 
	 */
	routerTo(path, params = {}, isLogin) {
		let objParams = params;
		// console.log(path, objParams, isLogin);
		// 
		if (~path.indexOf('http')) {
			this.$Route.push({
				path: '/pages/public/webview',
				query: {
					'webviewPath': path
				}
			})
			return false
		}
		// 
		if (path.indexOf('?') !== -1) {
			let index = path.lastIndexOf('?');
			let query = path.substring(index + 1, path.length);
			let arr = query.split('&')
			path = path.slice(0, index);
			arr.forEach(item => {
				let mArr = item.split('=');
				objParams[mArr[0]] = mArr[1]
			})
		}
		// tabbar
		if (isLogin) {
			router.replaceAll({
				path: path,
				query: objParams
			})
		} else {
			router.push({
				path: path,
				query: objParams
			})
		}
	},

	// 
	redirectTo(path, params = {}, isLogin) {
		// this.$store.dispatch('delVisitedViews', this.$route);
		let objParams = params;
		// 
		if (~path.indexOf('http')) {
			this.$Route.push({
				path: '/pages/public/webview',
				query: {
					'webviewPath': path
				}
			})
			return false
		}
		// 
		if (path.indexOf('?') !== -1) {
			let index = path.lastIndexOf('?');
			let query = path.substring(index + 1, path.length);
			let arr = query.split('&')
			path = path.slice(0, index);
			arr.forEach(item => {
				let mArr = item.split('=');
				objParams[mArr[0]] = mArr[1]
			})
		}
		// tabbar
		if (isLogin) {
			router.replaceAll({
				path: path,
				query: objParams
			})
		} else {
			router.replace({
				path: path,
				query: objParams
			})
		}
	},
	// 
	getParseComplexParams(params) {




		return JSON.parse(decodeURIComponent(decodeURIComponent(params)))

	},

	getQueryParam(key) {
		let pages = getCurrentPages()
		let page = pages[pages.length - 1]
		let options = page.options
		return options[key]
	}
}